import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, GiaSpLocalidade } from '../../../../models/dtos/mlf/giaSpLocalidade';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigoAuxiliar',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const GiaSpLocalidadeSearchView: FC<SearchViewProps<GiaSpLocalidade>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'uf.nome',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'UF',
    },
    {
      dataField: 'localidade.nome',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Cidade',
    },
    {
      dataField: 'codigoAuxiliar',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código Auxiliar',
    },
  ];
  return (
    <WindowComponent title="GIA SP - Código Auxiliar de Localidade" wikiId={35}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'GiaSpLocalidadeEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default GiaSpLocalidadeSearchView;
